<template>

  <div>
    <el-row type="flex"
      class="row-bg"
      justify="center">

    <div>
      <h1>登录界面</h1>
    用户名:
    <br>
    <el-input
      placeholder="请输入用户名"
      v-model="username"
      clearable
      maxlength="20">

    </el-input>
    <br>
    <br>

    密码:
    <br>
    <el-input
      placeholder="请输入密码"
      v-model="password"
      show-password
      maxlength="20">
    </el-input>
    <br>
    <br>

    
 
    <p class="toRegister" @click="toRegister"
      >没有账号?立即注册</p>

    </div>
    
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
  <el-button type="primary"
      plain id="login"
      @click="login">登录
    </el-button>
</el-row>

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'MyLogin',
  data() {
    return {
      username: '',
      password: '',
    }
  },

  methods: {
    onClickEnter() {
      this.login()
    },
    open1(msg) {
      this.$notify({
        title: '登录成功',
        message: '欢迎你' + msg,
        type: 'success',
      })
    },
    open4(msg) {
      this.$notify.error({
        title: 'error',
        message: msg,
      })
    },
    toRegister() {
      this.$router.push('/pc/register')
    },
    login() {
      axios({
        method: 'post',
        url: this.$baseURL+'/user/login',
        data: {
          username: this.username,
          password: this.password,
        },
      }).then((res) => {
        console.log(res)
        if (res.data.status == 200) {
          this.$store.state.uid = res.data.data.uid
          this.$store.state.username = res.data.data.username
          this.$store.state.nickname = res.data.data.nickname
          // console.log(this.$store.state.uid)
          // console.log(this.$store.state.username)
          // console.log(this.$store.state.nickname)
          this.open1(this.$store.state.nickname)
          this.$router.push('/pc/index')
        } else {
          this.open4('用户名或者密码错误')
        }
      })
    },
  },
  mounted() {
    this.$keyBoard(this, 'onClickEnter', 13) //13是enter按键  其他按键码自己查
    // console.log(this.$store.state.uid)
    // if (this.$store.state.uid != null&&this.$store.state.uid!==undefined) {

    //   this.$router.push('/index')
    // }
  },
}
</script>

<style scoped>
#login {
  
  width: 8rem;
}
.el-input {
  width: 25rem;
}
.toRegister{
  text-decoration: underline;
  color: blue;
}
</style>
